import { Injectable } from '@angular/core';

@Injectable()
export class PaginationService {
  public currentPage: number = 1;
  public totalPages: number = 10;
  public nPerPage: number = 20;
  public pages: any[] = [];
  public action: Function;
  public countItems: number;
  public text: string;
  public show: boolean = false;

  constructor() {}

  setParams(params) {
    this.totalPages = params.totalPages ? params.totalPages : this.totalPages;
    this.nPerPage = params.nPerPage ? params.nPerPage : this.nPerPage;
    this.currentPage = params.currentPage ? params.currentPage : this.currentPage;
    this.countItems = params.countItems ? params.countItems : this.countItems;
  }

  clear() {
      this.currentPage = 1;
      this.totalPages = 10;
      this.nPerPage = 20;
      this.pages = [];
      this.action = () => console.log('empty action');
      this.countItems = 0;
      this.text = null;
      this.show = false;
  }

  notShow() {
    this.show = false;
  }

  getPager() {
    this.show = true;
      //console.log(this.countItems, this.totalPages);
      // calculate total pages
    this.totalPages = Math.ceil(this.countItems / this.nPerPage);

    let startPage: number, endPage: number;
    if (this.totalPages <= 10) {
        // less than 10 total pages so show all
        startPage = 1;
        endPage = this.totalPages;
    } else {
        // more than 10 total pages so calculate start and end pages
        if (this.currentPage <= 6) {
            startPage = 1;
            endPage = 10;
        } else if (this.currentPage + 4 >= this.totalPages) {
            startPage = this.totalPages - 9;
            endPage = this.totalPages;
        } else {
            startPage = this.currentPage - 5;
            endPage = this.currentPage + 4;
        }
    }

    // calculate start and end item indexes
    let startIndex = (this.currentPage - 1) * this.nPerPage;
    let endIndex = Math.min(startIndex + this.nPerPage - 1, this.countItems - 1);

    // create an array of pages to ng-repeat in the pager control
    this.pages = this.range(startPage, endPage + 1);
  }

  setAction(action: Function) {
      this.action = action;
  }

  private range(start, stop, step?) {
    if (stop == null) {
      stop = start || 0;
      start = 0;
    }
    step = step || 1;

    let length = Math.max(Math.ceil((stop - start) / step), 0);
    let range = Array(length);

    for (let idx = 0; idx < length; idx++, start += step) {
      range[idx] = start;
    }

    return range;
  };

}



